#cart-overflow{
  overflow: hidden;
}

.shoppingCard-wrapper {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url('./shopping-bag.svg') no-repeat center center/cover;

  &:hover {
    cursor: pointer;
  }
  

  #cart-counter {
    font-size: 14px;
    font-weight: bold;
  }

 }